@font-face {
  font-family: 'Wremena';
  src: url('./assets/fonts/wremena_light.woff2') format('woff2'),
       url('./assets/fonts/wremena_light.woff') format('woff');
  font-weight: 300;
  font-style: normal;
}

:root {
  --font: "Wremena", serif;
  --color-black: #000;
  --color-white: #fff;
  --color-green: #025e14;
  --color-orange: #ff4c14;
  --color-red: #cc0000;
  --color-blue: #0000b5;
  --color-purple: #8b1fff;
}

html,
body,
#root {
  height: 100%;
}

body {
  margin: 0;
  background: var(--color-black);
  font-family: var(--font);
  font-weight: 300;
  overflow: hidden;
}

.map {
  height: 100%;
  background: var(--color-black);
  z-index: 0;

  .leaflet-control-layers,
  .leaflet-popup-close-button,
  .leaflet-popup-tip-container {
    display: none;
  }

  .leaflet-popup-content-wrapper {
    background: rgba(255, 255, 255, 0.9);
  }

  .leaflet-popup-content {
    font-family: var(--font);
    font-size: 1rem;
    hyphens: auto;
  }

}

.error-gps {
  position: fixed;
  top: 1rem;
  left: 1rem;
  background: var(--color-red);
  padding: 1rem;
  text-align: center;
  z-index: 1;
}

.icon-gps {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 2.5rem;
  color: var(--color-red);
  z-index: 1;
  pointer-events: none;
  filter: drop-shadow(0 0 10px var(--color-black));
}

.controls {
  position: fixed;
  right: 1rem;
  bottom: 1rem;
  z-index: 1;
  display: flex;
  flex-direction: column;

  button {
    font-size: 2rem;
    background: var(--color-white);
    border: 0;
    padding: 0;
    width: 3rem;
    height: 3rem;
    cursor: pointer;
    &:disabled {
      color: var(--color-black);
      background: var(--color-white);
      opacity: 0.7;
    }
  }
}

.info-toggle {
  position: fixed;
  top: 1rem;
  right: 1rem;
  border: 0;
  background: var(--color-blue);
  width: 2rem;
  height: 2rem;
  padding: 0;
  z-index: 4;
  cursor: pointer;
}

.info-overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(#cc0000, 0.9);
  font-size: 1.25rem;
  padding: 1rem;
  overflow: auto;
  z-index: 3;

  &__content {
    max-width: 46rem;
    font-size: clamp(1rem, 3vw, 1.5rem);
  }

  h1 {
    margin: -0.45rem 0 3.5rem;
    font-weight: 300;
    text-transform: uppercase;
    font-size: clamp(2rem, 4vw, 4rem);
  }
}

.transition-screen {
  --gradient-color: var(--color-red);
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: radial-gradient(circle, var(--color-white) 0%, var(--gradient-color) 100%);
  z-index: 5;
  font-size: clamp(1.5rem, 3vw, 2.5rem);
  text-transform: uppercase;

  &.green {
    --gradient-color: var(--color-green);
  }

  &.orange {
    --gradient-color: var(--color-orange);
  }

  &.blue {
    --gradient-color: var(--color-blue);
  }

  &.purple {
    --gradient-color: var(--color-purple);
  }
}
